.main {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3rem;
  flex-wrap: wrap;
  background-color: rgba(255, 228, 196, 0.664);
  padding: 1rem;
}
.product-div {
  border: 2px solid rgb(255, 196, 0);
  border-radius: 8px;
  width: 350px;
  height: 500px;
  position: relative;
  overflow: hidden;
  &:hover .product-title {
    visibility: visible;
    left: 0;
  }
  &:hover .btn {
    visibility: visible;
    left: 40%;
  }
  & .images {
    width: 100%;
    height: 100%;
    cursor: pointer;
    // object-fit: cover;
  }
  & .product-prices {
    color: purple;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 1.3rem;
    // border: 1px solid black;
    background-color: #f7d7e3aa;
    width: 40%;
    text-align: center;
    padding: 0.5rem;
    border-radius: 8px;
  }
  & .product-title {
    visibility: hidden;
    color: rgb(32, 30, 21);
    position: absolute;
    left: 250px;
    bottom: 10px;
    font-size: 1rem;
    // border: 1px solid black;
    background-color:goldenrod;
    width: 100%;
    height: 3rem;
    text-align: center;
    padding: 0.5rem;
    border-radius: 8px;
    transition: all 0.5s;
    cursor: pointer;
  }
}
.btn{
  // visibility: hidden;
    color: rgb(45, 41, 31);
    position: absolute;
    left: 350px;
    bottom: 80px;
    font-size: 1rem;
    border: 1px solid black;
    background-color:#df9a10cc;
    width: 5rem;
    height: 2.5rem;
    text-align: center;
    padding: 0.5rem;
    border-radius: 8px;
    transition: all 0.5s;
    cursor: pointer;
    &:hover{
      background-color: purple;
      color: white;
    }
}